@import "../../resources/variables.scss";

#hero {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  column-gap: 4rem;

  .hero-left {
    display: flex;
    flex-direction: column;
    justify-content: center;

    p {
      margin-top: 0;
    }
  }

  form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 3rem;

    label {
      display: flex;
    }

    .upload-button {
      border-color: $gunmetal;
      color: $gunmetal;
    }

    .submit-enabled {
      border-color: $red;
      color: $red;
    }

    input {
      display: none;
    }
  }

  img {
    max-width: 100%;
    max-height: 20rem;
  }

  .image-placeholder {
    height: 20rem;
    background-color: lightgrey;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
  }
}

@media (max-width: $mobile-width) {
  #hero {
    margin-top: 0;
    grid-template-columns: 1fr;
    row-gap: 2rem;

    .hero-left {
      p {
        text-align: center;
      }

      form {
        column-gap: 1rem;
      }
    }

    .hero-right {
      text-align: center;
    }

    .image-placeholder {
      height: 30vh;
    }

    img {
      max-height: 30vh;
    }
  }
}
